#root {
  background: #333;
  padding-top: 50px;
}
html {
  background: #333;
}
body {
  background: #333;
}
header {
  background: #333;
  color: white;
  z-index: 1;
  position: fixed;
  width: 100%;
  top: 0;
}
.topPadding {
  margin-top: 70px;
}
.box-container {
  /*padding-top: 20px;*/
  background: white;
  -webkit-box-shadow: 3px 3px 3px #999;
          box-shadow: 3px 3px 3px #999;
  padding-top: 1.5rem;
}
.box {
  padding: 5%;
  cursor: pointer;
}
.box div {
  height: 0;
  padding-bottom: 100%;
  border: 5px solid rgba(0,0,0,.5);
  border-radius: 50%;
  -webkit-transition: -webkit-transform .2s ease;
  transition: -webkit-transform .2s ease;
  -o-transition: transform .2s ease;
  transition: transform .2s ease;
  transition: transform .2s ease, -webkit-transform .2s ease;

}

.box:hover div{
  -webkit-transform: scale(1.03);
      -ms-transform: scale(1.03);
          transform: scale(1.03);
}

.box:active div{
  -webkit-transform: scale(.9);
      -ms-transform: scale(.9);
          transform: scale(.9);
}

.reactmodal {
  position: absolute;
  top: 50vh;
  left: 50vw;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.instructions {
  color: white;
  text-align: center;
  border: 1px solid white;
  border-radius: 5px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.instructions p {
  margin: 0;
}
.instructions .dismiss {
  text-decoration: underline;
  -webkit-transition: opacity .2s ease;
  -o-transition: opacity .2s ease;
  transition: opacity .2s ease;
}
.instructions .dismiss:hover {
  cursor:pointer;
  opacity: .75;
}
.fade-enter {
  opacity: 0;
  height: 0%;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
  height: 100%;
}

.fade-leave {
  opacity: 1;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  /*transform-origin: top;*/
  max-height: 100px;
}

.fade-leave.fade-leave-active {
  opacity: 0;
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
  -webkit-transform: scale(1,0);
      -ms-transform: scale(1,0);
          transform: scale(1,0);
  max-height: 0px;
}
.fadeOut {
  -webkit-animation: fade .25s forwards;
          animation: fade .25s forwards;
}

.fadeIn {
  -webkit-animation: fadeIn .25s;
          animation: fadeIn .25s;
}

@-webkit-keyframes fadeIn {
  0% {opacity: 0}
  100% {opacity: 1}
}

@keyframes fadeIn {
  0% {opacity: 0}
  100% {opacity: 1}
}

@-webkit-keyframes fade {
  0% {
    opacity: 1
  }
  100% {
    opacity: 0
  }
}

@keyframes fade {
  0% {
    opacity: 1
  }
  100% {
    opacity: 0
  }
}


